import { NgModule } from '@angular/core';
import { IonicPageModule } from 'ionic-angular';
import { ClipPopoverPage } from './clip-popover';
import { DirectiveModule } from '../../app/directives/directive.module';
import { ClipboardService, ClipboardModule } from 'ngx-clipboard';

@NgModule({
  declarations: [
    ClipPopoverPage
  ],
  imports: [
    IonicPageModule.forChild(ClipPopoverPage),
    ClipboardModule
  ],
  providers: [
    ClipboardService
  ],
  entryComponents: [
    ClipPopoverPage
  ]
})
export class ClipPopoverPageModule {}



